import React from 'react';
import './Home.css';
import City1 from './city1.png'
import City2 from './city2.png'
import City3 from './city3.png'
import City4 from './city4.png'
import City5 from './city5.png'

function Home() {
  const cities =[
    {
      image : City1,
      city:"img1"
    },
    {
      image : City2,
      city:"img2"
    },
    {
      image : City3,
      city:"img3"
    },
    {
      image : City4,
      city:"img4"
    },
    {
      image : City5,
      city:"img5"
    }
  ]
  return (
    <div className='class1'>
      <div className='class2'>
        {cities.map((city) => {
          return <div key={city.city} className='class3'>
            <img src={city.image} alt="" />
          </div>
        })}
      </div>
    </div>
  );
}

export default Home;
