import React, { useRef } from 'react';
import './Signup.css';
import Video from '../Source/Bgvideo.mp4';


function Signup() {
  const videoRef = useRef(null);

  const handleCanPlay = () => {
    if (videoRef.current) {
      videoRef.current.playbackRate = 0.1; // Set playback speed once the video can play
    }
  };

  return (
    <div className="signup-container">
      {/* Left Side */}
      <div className="signup-left-side">
        <video autoPlay loop muted className="signup-background-video" onCanPlay={handleCanPlay}>
          <source src={Video} type="video/mp4" />
          Your browser does not support the video tag.
        </video>
        <div className="signup-content">
          <h1>Welcome to</h1>
          <h2>VCode Community</h2>
          <p>Home to community of programming students worldwide</p>
        </div>
      </div>

      {/* Right Side */}
      <div className="signup-right-side">
        <h1>Join us</h1>
        <h2>Create a VCode account</h2>
        <p>Be part of programming students worldwide</p>
        <form className="signup-form">
          <input
            type="text"
            placeholder="Your username"
            className="signup-input-field"
          />
          
          <input
            type="password"
            placeholder="Your password"
            className="signup-input-field"
          />
          <input
            type="password"
            placeholder="Confirm your password"
            className="signup-input-field"
          />
          <button className="signup-button">Sign Up</button>
        </form>
        <p className="signup-login">
          Already have an account? <a href="/login">Log in</a>
        </p>
      </div>
    </div>
  );
}

export default Signup;
