import React,{useEffect, useRef} from 'react';
import './Login.css';
import Video from '../Source/Bgvideo.mp4';


function Login() {
  const videoRef = useRef(null);

  const handleCanPlay = () => {
    if (videoRef.current) {
      videoRef.current.playbackRate = 0.1; // Set playback speed once the video can play
    }
  };

  return (
    <div className='login-body'>
    <div className="container">
      {/* Left Side */}
      <div className="left-side">
         <video autoPlay loop muted className="background-video" onCanPlay={handleCanPlay} >
         <source src={Video} type="video/mp4" />
             Your browser does not support the video tag.
         </video>
        <div className="content">
          <h1>Welcome to</h1>
          <h2><span className='vcodeLogin'><span className='letterVlogin'>V</span>Code</span> Community</h2>
          
          <p>Home to community of programming students worldwide</p>
        </div>
      </div>

      {/* Right Side */}
      <div className="right-side">
        <h1>Welcome Back!</h1>
        <h2>Login to your account</h2>
        <p>It's nice to see you again. Ready to code?</p>
        <form className="login-form">
          <input
            type="text"
            placeholder="Your username or email"
            className="input-field"
          />
          <input
            type="password"
            placeholder="Your password"
            className="input-field"
          />
          <div className="options">
            
          </div>
          <button className="login-button">Log In</button>
        </form>
        <p className="signup">
          Don't have an account? <a href="/">Sign up</a>
        </p>
        </div>
    </div>
    </div>
  );
}

export default Login;
